import React, { Component } from 'react';
import classnames from 'classnames';
import { Row, Col } from '../UI/Grid';
import MediumPreview from '../Post/Preview/Medium';
import { postList } from '../Post/type';

export default class MediumPostList extends Component {
  static propTypes = {
    posts: postList.isRequired,
  };

  render() {
    const { posts } = this.props;
    const hideIndex = Math.max(posts.length - (posts.length % 2), 1);

    return (
      <Row className="medium-post-list">
        {posts.map((post, idx) => (
          <Col xs="12" sm="6" md="4" lg="4" xl="4" key={post.id} className={classnames('medium-post-list__item', { 'hide-sm-only': idx > hideIndex })}>
            <MediumPreview post={post} />
          </Col>
        ))}
      </Row>
    );
  }
}



