import React, { Component } from 'react';
import PictureMedium from '../Picture/Medium';
import DetailsInfo from '../Details/Info';
import DetailsComments from '../Details/Comments';
import PostLink from '../PostLink';
import { post } from '../type';

export default class MediumPreview extends Component {

  static propTypes = {
    post: post.isRequired,
  };

  render() {
    const { post } = this.props;

    return (
      <section className="medium-post-preview">
        <div className="medium-post-preview__wrapper">
          <PictureMedium post={post} />
          <div className="medium-post-preview__details">
            <div className="medium-post-preview__details-margin">
              <DetailsInfo post={post} />
              <PostLink post={post}>
                <h2>{post.title}</h2>
              </PostLink>
              <DetailsComments post={post} />
            </div>
          </div>
        </div>
      </section>
    );
  }
}



