import React, { Component } from 'react';
import Helmet from 'react-helmet';
import classnames from 'classnames';
import { getCanonical } from '../utils/url';
import config from '../../config.json';
import DisqusCount from '../components/Comments/DisqusCount';

export default class Page extends Component {
  render() {
    const {
            className,
            title,
            description = config.description,
            path,
            image       = config.image,
            date,
            posts,
            children,
          } = this.props;

    const siteTitle = `${title} | ${config.title}`;
    const url = getCanonical(config.siteUrl, path);
    const isPage = !posts;

    const schemaOrgJSONLD = [
      {
        '@context': 'http://schema.org',
        '@type': 'WebSite',
        url: config.siteUrl,
        name: config.title,
        alternateName: config.altName,
      },
    ];

    if (!isPage) {
      schemaOrgJSONLD.push([
        {
          '@context': 'http://schema.org',
          '@type': 'BreadcrumbList',
          itemListElement: posts.map(post => ({
            '@type': 'ListItem',
            position: 1,
            item: {
              '@id': post.permalink,
              name: title,
              image,
            },
          })),
        },
        {
          '@context': 'http://schema.org',
          '@type': 'BlogPosting',
          url: config.siteUrl,
          name: config.title,
          alternateName: '',
          headline: title,
          image: {
            '@type': 'ImageObject',
            url: image,
          },
          description,
        }]);
    }

    return (
      <section className={classnames('page', className)}>
        {children}
        <Helmet>
          <title>{siteTitle}</title>
          <meta name="author" content={config.author} />
          <meta name="description" content={description} />
          <meta name="keywords" content={config.keywords} />

          <script type="application/ld+json">
            {JSON.stringify(schemaOrgJSONLD)}
          </script>
          <meta name="image" content={image} />

          {isPage && <meta property="og:type" content="article" />}
          <meta property="og:title" content={title} />
          <meta property="og:url" content={url} />
          <meta property="og:site_name" content={config.title} />
          <meta property="og:description" content={description} />
          <meta property="og:image" content={image} />
          <meta property="og:updated_time" content={date} />

          <meta property="twitter:card" content="twitterCard" />
          <meta property="twitter:title" content={title} />
          <meta property="twitter:description" content={description} />
          <meta property="twitter:image" content={image} />
          <meta property="twitter:creator" content={`@${config.social.twitterId}`} />
          <meta property="twitter:site" content={`@${config.social.twitterId}`} />

          <meta property="fb:admins" content={config.social.facebookAdmins} />
          <meta property="fb:app_id" content={config.social.facebookId} />

          <meta property="vk:title" content={title} />
          <meta property="vk:url" content={url} />
          <meta property="vk:image" content={image} />
          <meta property="vk:description" content={description} />

          <link rel="publisher" href={config.social.googlePlusId} />
          <link rel="canonical" href={url} />

          <meta name="google-site-verification" content={config.analytics.gsv} />
        </Helmet>
        <DisqusCount />
      </section>
    );
  }
}


