import { string, number, func, object, shape, arrayOf, oneOfType } from 'prop-types';

export const image = shape({
  src: string.isRequired,
});

export const images = shape({
  heroPreviewLG: image.isRequired,
  heroPreviewMD: image.isRequired,
  heroPreviewSM: image.isRequired,
  mediumPreview: image.isRequired,
  smallPreview: image.isRequired,
});

export const post = shape({
  id: string.isRequired,
  title: string.isRequired,
  description: string.isRequired,
  color: string.isRequired,
  category: string.isRequired,
  tags: arrayOf(string).isRequired,
  slug: string.isRequired,
  permalink: string.isRequired,
  images: images.isRequired,
  date: string.isRequired,
  html: string,
  excerpt: string,
  type: string,
});

export const postList = arrayOf(post);


