import React, { Component } from 'react';
import Link from 'gatsby-link';
import moment from 'moment';
import { post } from '../type';

export default class Info extends Component {
  static propTypes = {
    post: post.isRequired,
  };

  render() {
    const { post, style, noDate } = this.props;
    return (
      <div className="post-details-info" style={style}>
        <Link to={`/${post.category}`} className="link">{post.category}</Link>
        <span>{noDate ? null : moment(post.date).format('YYYY / MM / DD')}</span>
      </div>
    );
  }
}



