import React, { Component } from 'react';
import classnames from 'classnames';

export default class Figure extends Component {
  render() {
    const { shaded, children, className } = this.props;
    return (
      <figure className={classnames('figure', { 'figure-shaded': shaded }, className)}>
        <picture>
          {children}
        </picture>
      </figure>
    );
  }
}



