import React, { Component } from 'react';
import classnames from 'classnames';

export class Grid extends Component {
  render() {
    const { className, children, style } = this.props;

    return (
      <div className={classnames('grid', className)} style={style}>
        {children}
      </div>
    );
  }
}

export class Row extends Component {
  render() {
    const { className, children, style } = this.props;

    return (
      <div className={classnames('row', className)} style={style}>
        {children}
      </div>
    );
  }
}

const sizeNames = ['xl', 'lg', 'md', 'sm', 'xs'];

export class Col extends Component {
  render() {
    const { className, style, children, ...sizes } = this.props;
    const classNames = sizeNames.reduce((result, name) => {
      if(name in sizes) {
        return result.concat([`col-${name}-${sizes[name]}`]);
      }
      return result;
    }, []);


    return (
      <div className={classnames('col', className, classNames)} style={style}>
        {children}
      </div>
    );
  }
}


