import React, { Component } from 'react';
import Figure from './Figure';
import { post } from '../type';

export default class Medium extends Component {
  static propTypes = {
    post: post.isRequired,
  };
  render() {
    const { post } = this.props;
    return (
      <Figure shaded={true}>
        <source srcSet={`${post.images.mediumPreview.src} 360w`} />
        <img src={post.images.mediumPreview.src} alt={post.title}  style={{ backgroundImage: `url(${post.images.blurUp.src})`, backgroundSize: 'cover' }} />
      </Figure>

    );
  }
}



