import React, { Component } from 'react';
import { string, func } from 'prop-types';

export default class Tag extends Component {
  static propTypes = {
    name: string.isRequired,
    mount: func,
    unmount: func,
    onLoad: func,
  };

  static defaultProps = {
    mount: element => document.body.appendChild(element),
    unmount: element => element.parentNode.removeChild(element),
  };

  componentDidMount() {
    const { name, mount, onLoad, ...attrs } = this.props;
    const element = document.createElement(name);
    Object.keys(attrs).forEach(key => element[key] = attrs[key]);
    element.onload = onLoad;
    this.element = element;
    mount(element, this);
  }

  componentWillUnmount() {
    const { unmount } = this.props;
    unmount(this.element, this);
  }

  render() {
    return null;
  }
}



