import React, { Component } from 'react';

export default class Google extends Component {

  state = {
    seed: Math.random(),
  };

  componentDidMount() {
    (window.adsbygoogle = window.adsbygoogle || []).push({});
    return;
    this.timerId = setInterval(() => {
      this.setState({ seed: Math.random()});
      (window.adsbygoogle = window.adsbygoogle || []).push({});
    }, 30000);
  }

  componentWillUnmount() {
    clearInterval(this.timerId);
  }

  render() {
    const { className = '', client, slot, format, type = 'rect' } = this.props;
    const { seed } = this.state;
    // avoid replace parent in tag by google ads

    return (
      <div
        key={seed}
        className={`ad-google ad-google--${type} ${className}`}
        dangerouslySetInnerHTML={{
          __html: `<ins
        class="adsbygoogle adsbygoogle-${type}"
        data-ad-client="${client}"
        data-ad-slot="${slot}"
        data-ad-format="${format}"></ins>`,
        }}
      />
    );
  }
}



