import React, { Component } from 'react';
import Figure from './Figure';
import { post } from '../type';

export default class Hero extends Component {
  static propTypes = {
    post: post.isRequired,
  };

  render() {
    const { post, shaded } = this.props;
    return (
      <Figure shaded={shaded}>
        <img src={post.images.heroPreviewLG.src} alt={post.title} style={{ backgroundImage: `url(${post.images.blurUp.src})`, backgroundSize: 'cover' }} />
      </Figure>

    );
  }
}



